header {
    height: 60px;
    border-bottom: solid 2px #03A9F4;
}

.menu {
    height: 100%;
    display: none;
    
    
}

.menu nav {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    
}


.menuItem {
    height:100%;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    letter-spacing: 2px;
    width: 70px;
    -ms-flex-pack: center;
        justify-content: center;
}

.menuItem h4:hover {
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    letter-spacing: 4px;
    background: var(--primary-bg-color);
    color: white;
}

.menuItem h4 {
    font-size: 1.3em;
    height: 60px;
    width:120%;
    -ms-flex-align: center;
        align-items: center; 
    display: -ms-flexbox; 
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.menuItem a{
    text-decoration: none;
    color: black;
}








/*
mobile menu*/
.mobileBar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.toggleMenu {
  float: right;
  height: 26px;

  width: 40px;
  margin: 17px;
  display: inline-block;
  }

  .bars {
  top: 5px;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 100%;
}

.bar {
  height: 4px;
  width: 100%;
  background: black;
  border-radius: 24px;
}


.our-logo {
    height: 58px;
    width: 68px;
}


.mobile-menu {

  position: fixed;
  width: 100%;
  height: 100%;
  left:100%;
  background: #ededff;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  display: none;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  top: 60px;
}
.show-mobile-menu {
  left: 0%;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}

.mobile-menu-row {
  width: 100%;
  text-align: center;
  margin: 3px 0  ;
  background: #03A9F4;
  border-color: rgb(173, 216, 230);
  border-width: 3px 0 3px;
  border-style: solid;

  -webkit-transition: .3s;

  -o-transition: .3s;

  transition: .3s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.mobile-menu-row:hover {
    background: rgb(173, 216, 230);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    letter-spacing: 4px;

}

.mobile-menu-row:hover a {
    color: grey;

}
.mobile-menu-row a {
    font-size: 2.5em;
    text-decoration: none;
    color: #ead7d7;
    width: 100%;
    color: white;

}


@media only screen and (min-width: 500px) {

  .menu {
      height: 100%;
      display: block;
  }

  .mobileBar {
    display: none;
}












}



